import DataService from "../../service/DataService";

let buttons = [
  {
    title: "Add Check Out Request",
    iconClass: "fa fa-plus",
    callbackfunction: "AddCheckOut",
    additionalClass: "",
    isdisabled: false,
    isvisible: true,
    checkPrivilege: false,
  },
];

let leftsearchSchema = [
  {
    fieldName: "Product Name",
    fieldType: "text",
    fieldIcon: "fa-user",
    paramName: "UniqueNames",
    fieldSearchConditionName: [""],
    isSearch: false,
    value: "",
    listOptions: [],
  },
  {
    fieldName: "Unique Name",
    fieldType: "ddl-check",
    fieldIcon: "fa-server",
    paramName: "catalogId",
    fieldSearchConditionName: [""],
    isSearch: false,
    value: "",
    listOptions: [],
  },
];

let listActions = [
  {
    title: "Reject",
    href: "",
    icon: "fa fa-times-circle action-icon text-danger",
    additionalSpan: "",
  },
];

function bindleftCommonSearchdropdown(instance, modulename, filtername) {
  var vm = instance;
  var url = `moduleName=${modulename}&department_ids=`;
  DataService.GetCompanyUsers(url).then((response) => {
    var json = response.data;
    if (json != null) {
      json.forEach(function (item, index) {
        let obj = {
          name: item.USERNAME,
          value: `${item.USER_ID}`,
        };
        vm.leftSearchFields[1].listOptions.push(obj);
      });
      return vm.LeadStatusList;
    }
  });
}

export default {
  buttons,
  leftsearchSchema,
  listActions,
  bindleftCommonSearchdropdown,
};
